import { InfoCircleOutlined } from "@ant-design/icons";
import { Input, notification, Tooltip } from "antd";
import classNames from "classnames";
import React, { useContext, useEffect, useState } from "react";
import Footer from "_components/common/Footer";
import RoundedButton from "_components/common/RoundedButton";
import { BigLogo } from "_components/common/WalletIcons";
import {
  getRewardsTooltip,
  getRewardsTooltipColor,
  nodeAddressTooltip,
} from "_constants/general";
import StoreContext from "_context/storeContext";
import { sendAuthenticationRequest } from "_src/services/authenticationService";
import "./index.scss";

const { TextArea } = Input;

export interface LoginProps {
  isHandshaked: boolean;
  ppNodeUrl: string;
}

const LoginPage: React.FC<LoginProps> = ({ isHandshaked, ppNodeUrl }) => {
  const [webToken, setWebToken] = useState("");
  const [nodeAddress, setNodeAddress] = useState("");
  const [isConnected, setIsConnected] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);

  const store = useContext(StoreContext);

  const { appStore } = store;

  const getData = () => {
    fetch("node_default_config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        const { wsUrl } = myJson;

        if (wsUrl) {
          setNodeAddress((_state) => wsUrl);
        }
      })
      .catch((err) => {
        console.log("could not fetch json file. error -", err);
      });
  };

  useEffect(() => {
    getData();
  }, []);

  async function handlePasteClick() {
    if (!isConnected) {
      return;
    }
    const ourLink = await navigator.clipboard.readText();
    setWebToken(ourLink);
    appStore.setSocketError("");
  }

  async function handlePasteAddressClick() {
    if (isConnected) {
      return;
    }
    const ourLink = await navigator.clipboard.readText();
    setNodeAddress(ourLink);
    appStore.setSocketError("");
  }

  useEffect(() => {
    setIsConnected((_oldState) => isHandshaked);
    if (isHandshaked) {
      notification.success({
        message: "Success",
        description: `Connected to ${ppNodeUrl}`,
      });
    }

    setIsConnecting(false);
  }, [isHandshaked]);

  useEffect(() => {
    if (appStore.socketError) {
      notification.error({
        message: "Could not authenticate",
        description: `${appStore.socketError}`,
      });
      setIsConnecting(false);
    }
  }, [appStore.socketError]);

  const [
    ourDataFromSocket,
    connectToSocketHandler,
    disconnectFromSocketHandler,
    sendMessageHandler,
  ] = store.appStore.socketData;

  const handleTokenChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setWebToken((_prev) => e.currentTarget.value);
    appStore.setSocketError("");
  };

  const handleNodeAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const aa = e.currentTarget.value;
    setNodeAddress((_prev) => aa);
  };

  const handleConfirmLink = () => {
    if (!webToken) {
      return;
    }
    sendAuthenticationRequest(webToken);
  };

  const handleConnectLink = () => {
    if (!nodeAddress) {
      return;
    }
    setIsConnecting(true);
    console.log("setting socket url", nodeAddress);
    appStore.setSocketUrl(nodeAddress);
  };

  const handleDisconnectLink = () => {
    disconnectFromSocketHandler();
  };

  const isConnectingClasses = isConnecting ? "button is-loading" : " ";
  console.log("ðŸš€ ~ file: index.tsx ~ line 125 ~ isConnecting", isConnecting);
  console.log(
    "ðŸš€ ~ file: index.tsx ~ line 124 ~ isConnectingClasses",
    isConnectingClasses
  );

  const isDisconnected = !isConnected;

  return (
    <div className="main-page-container columns">
      <section className="section page-login column">
        <div className="container">
          <div className="block block-container">
            <div className="login-logo-container">
              <figure className="image">{BigLogo()}</figure>

              <p className="logo-title is-size-4-desktop is-size-5-touch">
                Node Monitor
              </p>
            </div>
            <div className="form-container">
              <div className="form-title is-flex  is-align-items-center">
                <div className="form-logo-title-container">
                  <p className="form-logo-title is-size-4-touch is-size-5-desktop">
                    Enter Your Node address
                  </p>
                  <Tooltip
                    title={nodeAddressTooltip}
                    placement="top"
                    color={getRewardsTooltipColor}
                  >
                    <InfoCircleOutlined
                      style={{ fontSize: 18, marginTop: -5 }}
                    />
                  </Tooltip>
                </div>
                <div className="paste-button-container is-flex is-align-items-center ">
                  <RoundedButton
                    title={"Paste from clipboard"}
                    width="250px"
                    className={classNames("1button button-submit ", {
                      inactive: isConnected,
                    })}
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      color: "white",
                    }}
                    containerClassName="is-pulled-right"
                    handleClick={handlePasteAddressClick}
                  />
                </div>
              </div>
              <div className="form-content ">
                <div className="link-container input-container">
                  <div className="link-input-container">
                    <Input
                      value={nodeAddress}
                      disabled={isConnected}
                      placeholder="Node address"
                      onChange={handleNodeAddressChange}
                    />
                  </div>
                </div>
                <div className="go-button-container double-button is-flex is-justify-content-space-between is-align-content-space-between ">
                  <RoundedButton
                    title={"Connect"}
                    width="250px"
                    className={classNames(
                      "1button button-submit ",
                      {
                        inactive: isConnected,
                      },
                      isConnectingClasses
                    )}
                    disabled={isConnected}
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                    containerClassName="is-pulled-right"
                    handleClick={handleConnectLink}
                  />
                  <RoundedButton
                    title={"Disconnect"}
                    width="250px"
                    className={classNames("1button button-submit ", {
                      inactive: isDisconnected,
                    })}
                    disabled={isDisconnected}
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                    containerClassName="is-pulled-right"
                    handleClick={handleDisconnectLink}
                  />
                </div>
              </div>
            </div>

            <div className="form-container">
              <div className="form-title is-flex  is-align-items-center">
                <div className="form-logo-title-container">
                  <p className="form-logo-title is-size-4-touch is-size-5-desktop">
                    Enter Your Token to Access
                  </p>
                  <Tooltip
                    title={getRewardsTooltip}
                    placement="top"
                    color={getRewardsTooltipColor}
                  >
                    <InfoCircleOutlined
                      style={{ fontSize: 18, marginTop: -5 }}
                    />
                  </Tooltip>
                </div>
                <div className="paste-button-container is-flex is-align-items-center ">
                  <RoundedButton
                    title={"Paste from clipboard"}
                    width="250px"
                    className={classNames(
                      "1button button-submit ",
                      {
                        inactive: isDisconnected,
                      }
                      // activeClasses
                    )}
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      color: "white",
                    }}
                    containerClassName="is-pulled-right"
                    handleClick={handlePasteClick}
                  />
                </div>
              </div>
              <div className="form-content ">
                <div className="link-container">
                  <div className="link-input-container">
                    <TextArea
                      value={webToken}
                      rows={6}
                      size="large"
                      onChange={handleTokenChange}
                      disabled={isDisconnected}
                      placeholder="Authentication Token"
                    />

                    {isConnected && appStore.socketError && (
                      <p className="error-message has-text-danger has-text-centered">
                        Error. You are not authorized
                      </p>
                    )}
                  </div>
                </div>
                <div className="go-button-container is-flex is-justify-content-center is-align-items-center ">
                  <RoundedButton
                    title={"Go"}
                    width="250px"
                    className={classNames(
                      "1button button-submit ",
                      {
                        inactive: isDisconnected,
                      }
                      // activeClasses
                    )}
                    disabled={isDisconnected}
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                    containerClassName="is-pulled-right"
                    handleClick={handleConfirmLink}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
};

export default LoginPage;
